"use client"

import { DashboardLayout } from "@/components/layouts/dashboard-layout"
import { Card } from "@/components/ui/card"
import { ChartContainer, ChartTooltip, ChartTooltipContent } from "@/components/ui/chart"
import { AreaChart, Area, XAxis, YAxis, ResponsiveContainer } from "recharts"
import { PeriodSelector } from "@/components/period-selector"
import { OptimizedBarChart3D } from "@/components/3d/optimized-bar-chart"
import { useState } from "react"

// Generate mock data based on selected period
const generateMockData = (period: string) => {
  switch (period) {
    case "week":
      return [
        { date: "Mon", income: 1200, expenses: 800 },
        { date: "Tue", income: 1500, expenses: 1000 },
        { date: "Wed", income: 1300, expenses: 900 },
        { date: "Thu", income: 1700, expenses: 1200 },
        { date: "Fri", income: 2000, expenses: 1500 },
        { date: "Sat", income: 1800, expenses: 1300 },
        { date: "Sun", income: 1400, expenses: 950 },
      ]
    case "quarter":
      return [
        { date: "Jan", income: 12000, expenses: 8000 },
        { date: "Feb", income: 13500, expenses: 9000 },
        { date: "Mar", income: 15000, expenses: 10000 },
      ]
    case "year":
      return [
        { date: "Q1", income: 40000, expenses: 28000 },
        { date: "Q2", income: 45000, expenses: 30000 },
        { date: "Q3", income: 42000, expenses: 29000 },
        { date: "Q4", income: 48000, expenses: 32000 },
      ]
    case "month":
    default:
      return [
        { date: "Jan", income: 4000, expenses: 2400 },
        { date: "Feb", income: 3000, expenses: 1398 },
        { date: "Mar", income: 2000, expenses: 9800 },
        { date: "Apr", income: 2780, expenses: 3908 },
        { date: "May", income: 1890, expenses: 4800 },
        { date: "Jun", income: 2390, expenses: 3800 },
      ]
  }
}

const chartConfig = {
  income: {
    label: "Income",
    theme: {
      light: "var(--chart-1)",
      dark: "var(--chart-1)",
    },
  },
  expenses: {
    label: "Expenses",
    theme: {
      light: "var(--chart-2)",
      dark: "var(--chart-2)",
    },
  },
}

const categoryData = [
  { label: "Shopping", value: 2500, color: "#FF6B6B" },
  { label: "Bills", value: 1800, color: "#4ECDC4" },
  { label: "Food", value: 1200, color: "#45B7D1" },
  { label: "Transport", value: 800, color: "#96CEB4" },
  { label: "Entertainment", value: 600, color: "#FFEEAD" },
]

export default function DashboardPage() {
  const [selectedPeriod, setSelectedPeriod] = useState("month")
  const data = generateMockData(selectedPeriod)

  return (
    <DashboardLayout>
      <div className="grid gap-6">
        <div className="flex items-center justify-between">
          <h1 className="text-3xl font-bold">Dashboard</h1>
          <PeriodSelector value={selectedPeriod} onChange={setSelectedPeriod} />
        </div>

        <div className="grid gap-6 md:grid-cols-3">
          <DraggableWidget 
            defaultPosition={positions["balance"] || { x: 0, y: 0 }}
            onPositionChange={(pos) => updatePosition("balance", pos)}
          >
            <div className="p-6">
              <div className="flex flex-col gap-2">
                <h3 className="text-sm font-medium text-muted-foreground">
                  Total Balance
                </h3>
                <div className="text-2xl font-bold">$24,563.00</div>
                <div className="text-sm text-muted-foreground">
                  +2.5% from last month
                </div>
              </div>
            </div>
          </DraggableWidget>
          <DraggableWidget
            defaultPosition={positions["income"] || { x: 0, y: 0 }}
            onPositionChange={(pos) => updatePosition("income", pos)}
          >
            <div className="p-6">
              <div className="flex flex-col gap-2">
                <h3 className="text-sm font-medium text-muted-foreground">
                  Monthly Income
                </h3>
                <div className="text-2xl font-bold">$8,423.00</div>
                <div className="text-sm text-muted-foreground">
                  +4.1% from last month
                </div>
              </div>
            </div>
          </DraggableWidget>
          <DraggableWidget
            defaultPosition={positions["expenses"] || { x: 0, y: 0 }}
            onPositionChange={(pos) => updatePosition("expenses", pos)}
          >
            <div className="p-6">
              <div className="flex flex-col gap-2">
                <h3 className="text-sm font-medium text-muted-foreground">
                  Monthly Expenses
                </h3>
                <div className="text-2xl font-bold">$5,823.00</div>
                <div className="text-sm text-muted-foreground">
                  -1.2% from last month
                </div>
              </div>
            </div>
          </DraggableWidget>
        </div>

        <div className="grid gap-6 lg:grid-cols-2">
          <Card className="p-6 transition-all duration-200 hover:shadow-lg">
            <div className="mb-4">
              <h3 className="text-lg font-medium">Income vs Expenses</h3>
              <p className="text-sm text-muted-foreground">
                Compare your income and expenses over time
              </p>
            </div>
            <div className="aspect-[2/1] w-full">
              <ChartContainer config={chartConfig}>
                <ResponsiveContainer>
                  <AreaChart data={data}>
                    <defs>
                      <linearGradient id="income" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="0%" stopColor="var(--chart-1)" stopOpacity={0.2} />
                        <stop offset="100%" stopColor="var(--chart-1)" stopOpacity={0} />
                      </linearGradient>
                      <linearGradient id="expenses" x1="0" y1="0" x2="0" y2="1">
                        <stop offset="0%" stopColor="var(--chart-2)" stopOpacity={0.2} />
                        <stop offset="100%" stopColor="var(--chart-2)" stopOpacity={0} />
                      </linearGradient>
                    </defs>
                    <XAxis
                      dataKey="date"
                      stroke="var(--muted-foreground)"
                      fontSize={12}
                      tickLine={false}
                      axisLine={false}
                    />
                    <YAxis
                      stroke="var(--muted-foreground)"
                      fontSize={12}
                      tickLine={false}
                      axisLine={false}
                      tickFormatter={(value) => `$${value}`}
                    />
                    <ChartTooltip
                      content={({ active, payload }) => (
                        <ChartTooltipContent
                          active={active}
                          payload={payload}
                          formatter={(value) =>
                            new Intl.NumberFormat("en-US", {
                              style: "currency",
                              currency: "USD",
                            }).format(value as number)
                          }
                        />
                      )}
                    />
                    <Area
                      type="monotone"
                      dataKey="income"
                      stroke="var(--chart-1)"
                      fill="url(#income)"
                      strokeWidth={2}
                    />
                    <Area
                      type="monotone"
                      dataKey="expenses"
                      stroke="var(--chart-2)"
                      fill="url(#expenses)"
                      strokeWidth={2}
                    />
                  </AreaChart>
                </ResponsiveContainer>
              </ChartContainer>
            </div>
          </Card>

          <Card className="p-6 transition-all duration-200 hover:shadow-lg">
            <div className="mb-4">
              <h3 className="text-lg font-medium">Expense Categories</h3>
              <p className="text-sm text-muted-foreground">
                Breakdown of your expenses by category
              </p>
            </div>
            <OptimizedBarChart3D data={categoryData} />
          </Card>
        </div>
      </div>
    </DashboardLayout>
  )
}
