"use client"

import { Button } from "@/components/ui/button"
import { ScrollArea } from "@/components/ui/scroll-area"
import { cn } from "@/lib/utils"
import { 
  ChartBarIcon, 
  CreditCardIcon, 
  DocumentTextIcon,
  CogIcon,
  ChevronLeftIcon,
  ChevronRightIcon,
  HomeIcon,
  BanknotesIcon,
  DocumentChartBarIcon,
  BellIcon
} from "@heroicons/react/24/outline"
import Link from "next/link"
import { usePathname } from "next/navigation"

const sidebarLinks = [
  {
    title: "Dashboard",
    href: "/dashboard",
    icon: HomeIcon,
  },
  {
    title: "Transactions",
    href: "/transactions",
    icon: CreditCardIcon,
  },
  {
    title: "Budgets",
    href: "/budgets",
    icon: BanknotesIcon,
  },
  {
    title: "Reports",
    href: "/reports",
    icon: DocumentChartBarIcon,
  },
  {
    title: "Analytics",
    href: "/analytics",
    icon: ChartBarIcon,
  },
  {
    title: "Documents",
    href: "/documents",
    icon: DocumentTextIcon,
  },
  {
    title: "Notifications",
    href: "/notifications",
    icon: BellIcon,
  },
  {
    title: "Settings",
    href: "/settings",
    icon: CogIcon,
  },
]

interface SidebarProps {
  open?: boolean
  onOpenChange?: (open: boolean) => void
  className?: string
}

export function Sidebar({ open = true, onOpenChange, className }: SidebarProps) {
  const pathname = usePathname()

  return (
    <aside
      className={cn(
        "border-r bg-card flex h-full w-[280px] flex-col transition-all duration-300",
        !open && "w-[80px]",
        className
      )}
    >
      <div className="flex h-[60px] items-center justify-between border-b px-6">
        <Link href="/dashboard" className="flex items-center gap-2">
          {open ? (
            <>
              <BanknotesIcon className="h-6 w-6" />
              <span className="font-semibold">Finance Tracker</span>
            </>
          ) : (
            <BanknotesIcon className="h-6 w-6" />
          )}
        </Link>
        <Button
          variant="ghost"
          size="icon"
          onClick={() => onOpenChange?.(!open)}
          className="h-8 w-8"
        >
          {open ? (
            <ChevronLeftIcon className="h-4 w-4" />
          ) : (
            <ChevronRightIcon className="h-4 w-4" />
          )}
        </Button>
      </div>
      <ScrollArea className="flex-1 py-4">
        <nav className="grid gap-1 px-4">
          {sidebarLinks.map((link) => {
            const Icon = link.icon
            const isActive = pathname === link.href

            return (
              <Link
                key={link.href}
                href={link.href}
                className={cn(
                  "flex items-center gap-3 rounded-lg px-3 py-2 text-sm transition-colors",
                  isActive
                    ? "bg-accent text-accent-foreground"
                    : "text-muted-foreground hover:bg-accent hover:text-accent-foreground"
                )}
              >
                <Icon className="h-4 w-4" />
                {open && <span>{link.title}</span>}
              </Link>
            )
          })}
        </nav>
      </ScrollArea>
    </aside>
  )
}
