"use client"

import { useRef, useState } from "react"
import { Canvas, useFrame } from "@react-three/fiber"
import { OrbitControls, Text, Environment, Float } from "@react-three/drei"
import * as THREE from "three"

interface BarProps {
  position: [number, number, number]
  height: number
  color: string
  label: string
  value: number
}

function Bar({ position, height, color, label, value }: BarProps) {
  const mesh = useRef<THREE.Mesh>(null!)
  const [hovered, setHovered] = useState(false)

  useFrame((state, delta) => {
    const targetScaleY = height
    const targetScaleXZ = hovered ? 1.1 : 1
    const lerpFactor = 0.15

    // Smooth height animation
    mesh.current.scale.y = THREE.MathUtils.lerp(
      mesh.current.scale.y,
      targetScaleY,
      lerpFactor
    )

    // Smooth hover effect
    mesh.current.scale.x = THREE.MathUtils.lerp(
      mesh.current.scale.x,
      targetScaleXZ,
      lerpFactor
    )
    mesh.current.scale.z = THREE.MathUtils.lerp(
      mesh.current.scale.z,
      targetScaleXZ,
      lerpFactor
    )

    // Optimized floating animation
    if (!hovered) {
      const time = state.clock.getElapsedTime()
      mesh.current.position.y = Math.sin(time) * 0.02
    }
  })

  return (
    <group position={position}>
      <mesh
        ref={mesh}
        scale={[1, 0.1, 1]}
        onPointerOver={() => setHovered(true)}
        onPointerOut={() => setHovered(false)}
        castShadow
        receiveShadow
      >
        <boxGeometry args={[0.8, 1, 0.8]} />
        <meshStandardMaterial
          color={hovered ? "#fff" : color}
          metalness={0.6}
          roughness={0.3}
          emissive={color}
          emissiveIntensity={hovered ? 0.5 : 0.2}
        />
      </mesh>
      <Text
        position={[0, -0.5, 0]}
        rotation={[-Math.PI / 2, 0, 0]}
        fontSize={0.3}
        color={color}
        anchorX="center"
        anchorY="middle"
      >
        {label}
      </Text>
      <Text
        position={[0, height + 0.5, 0]}
        rotation={[0, 0, 0]}
        fontSize={0.3}
        color={color}
        anchorX="center"
        anchorY="middle"
      >
        ${value.toLocaleString()}
      </Text>
    </group>
  )
}

interface BarChart3DProps {
  data: Array<{
    label: string
    value: number
    color: string
  }>
  className?: string
}

export function OptimizedBarChart3D({ data, className }: BarChart3DProps) {
  const maxValue = Math.max(...data.map((d) => d.value))
  const normalizedData = data.map((d) => ({
    ...d,
    height: (d.value / maxValue) * 5,
  }))

  return (
    <div className={`h-[400px] w-full ${className}`}>
      <Canvas
        camera={{ position: [5, 5, 5], fov: 60 }}
        shadows="soft"
        dpr={[1, 2]}
        gl={{ 
          antialias: true,
          alpha: true,
          powerPreference: "high-performance"
        }}
        performance={{ min: 0.5 }}
      >
        <ambientLight intensity={0.5} />
        <directionalLight
          position={[5, 5, 5]}
          intensity={1}
          castShadow
          shadow-mapSize={[1024, 1024]}
        />
        <Environment preset="city" />
        
        <group position={[0, 0, 0]}>
          {normalizedData.map((item, i) => (
            <Bar
              key={item.label}
              position={[
                (i - (normalizedData.length - 1) / 2) * 2,
                item.height / 2,
                0,
              ]}
              height={item.height}
              color={item.color}
              label={item.label}
              value={item.value}
            />
          ))}
        </group>
        <OrbitControls
          enablePan={false}
          enableZoom={false}
          minPolarAngle={Math.PI / 4}
          maxPolarAngle={Math.PI / 2}
          autoRotate
          autoRotateSpeed={0.5}
        />
        <gridHelper args={[20, 20, "#333333", "#222222"]} />
      </Canvas>
    </div>
  )
}
